body {
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Helvetica Neue, Arial, Noto Sans, sans-serif;
}

.container {
  max-width: 1200px;
}

.card {
  border: 1px solid #e9ecef;
  box-shadow: 0 1px 6px #0000000f;
}

.form-label {
  margin-bottom: .25rem;
}

.form-text, small.text-muted {
  font-size: .8rem;
}

pre {
  background: #f8f9fa;
  border-radius: 4px;
  padding: .5rem;
  overflow-x: auto;
}

@media (width <= 991.98px) {
  .container {
    padding-left: 8px;
    padding-right: 8px;
  }

  .card {
    padding: .75rem !important;
  }
}

.btn {
  white-space: nowrap;
}

ul {
  padding-left: 1.2rem;
}
